
.Success, .Error {
    display: flex;
    height: 80px;
    width: 90%;
    margin: 50px;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    border-radius: 10px;
    box-shadow: 10px 10px 20px rgba(0, 0, 0, 0.3);
}

.Success h2, .Error h2 {
    margin: 2px 20px;
    font-size: 3rem;
}

.Success p, .Error p {
    margin: 15px 20px;
    text-align: center;
    font-size: 2rem;
}

@media screen and (max-width: 920px) {
    
    .Success p, .Error p {
        font-size: 1.7rem;
        margin: 20px;
    }

    .Success h2, .Error h2 {
        font-size: 2.5rem;
        margin: 5px 20px;
    }
}

@media screen and (max-width: 800px) {
    
    .Success p, .Error p {
        font-size: 1.2rem;
    }

    .Success h2, .Error h2 {
        font-size: 2rem;
        margin: 10px 20px;
    }
}

@media screen and (max-width: 610px) {
    
    .Success p, .Error p {
        font-size: 1rem;
        margin: 30px 20px;
    }

    .Success h2, .Error h2 {
        font-size: 1.5rem;
        margin: 20px;
    }

    .Success, .Error {
        margin: 30px;
    }
}

@media screen and (max-width: 510px) {
    
    .Success p, .Error p {
        font-size: 0.8rem;
    }
}

.Success {
    background-color: #cdf0d1;
    outline: 5px solid #8aff97;
}

.Success h2 {
    color: #1ac803;
}

.Success p {
    color: #1ac803;
}


.Error {
    background-color: #f0cdcd;
    outline: 5px solid #ff8a8a;
}

.Error h2 {
    color: #c80303;
}

.Error p {
    color: #c80303;
}
