
.navbar{
    width: 100%;
    height: 50px;
    background-color: rgb(31, 41, 55);
    display: flex;
}

.nav-logo {
    width: 70px;
    height: 100%;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    cursor: pointer;
}

.nav-toggle {
    display: grid;
    grid-template-columns: 1fr 1fr;
    margin: 15px 20px;
}

.nav-toggle li{
    list-style: none;
}

.nav-toggle li a{
    color: antiquewhite;
    text-decoration: none;
    padding: 10px;
    font-size: 1.25rem;
}

.nav-toggle li a:hover{
    color: white;
}

.heading {
    width: 700px;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-right: 150px;
}

.heading h1{
    width: 60%;
    color: aliceblue;
    text-align: center;
}

.nav-btn {
    position: absolute;
    top: 12px;
    right: 0;
    width: 90px;
    margin-right: 20px;
    padding: 5px 15px;
    cursor: pointer;
    background-color: #1869ff;
    color: antiquewhite;
    border: none;
    border-radius: 20px;
    font-size: 1rem;
    transition: transform(1s);
}

.nav-btn:hover {
    transform: scale(0.9);
    background-color: #0059ff;
    border: 1px solid white;
}

@media screen and (max-width: 1050px) {
        .heading {
            width: 500px;
        }

        .heading h1{
            width: 100%;
            font-size: 25px;
        }
  }

@media screen and (max-width: 800px) {
        .heading {
            width: 300px;
        }

        .heading h1{
            font-size: 18px;
        }
  }

@media screen and (max-width: 520px) {
        .heading {
            width: 200px;
        }

        .heading h1{
            font-size: 15px;
        }
  }
